<!-- ListItem -->
<template>
    <div>
        <li class="todo-list-p" @mouseenter="handleMove(true)" @mouseleave="handleMove(false)" :style="{color:color}">
            <span>
                <label>
                    <input type="checkbox" class="todo-list-check" v-model="item.complete">
                    <span>{{item.title}}</span>
                </label>
            
            </span>
            <span class="del-this-btn" v-show="isShow" @click="del">删除</span>
        </li>
    </div>
</template>

<script>
import PubSub from 'pubsub-js'
export default {
data () {
    return {
        color:'#000',
        isShow:false
    }
},

props: {
    item:Object,
    index:Number,
    // delItem:Function
},

components: {},

created () {},

computed: {},

mounted (){},

methods: {
        handleMove(isEnter){
        // console.log('鼠标移入移出')
        if(isEnter){
            this.color = 'blue',
            this.isShow = true
        }else{
            this.color = '#000'
            this.isShow = false
        }
    },
    del(){
        console.log('删除 index',this.index)
        // const { item,index,delItem } = this
        const { item,index } = this
        if(window.confirm(`确认删除${item.title}吗？`)){
            // delItem(index)
            PubSub.publish('delItem',index)  // 发布消息
        }
    }
}
}

</script>
<style>
.todo-list-p{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 10px;
    box-sizing: border-box;
    margin: 0;
    height: 40px;
    text-align: left;
    border-bottom: 1px solid gray;
}
.todo-list-p:last-child{
    border-bottom: 0;
}
.del-this-btn{
    padding: 5px 10px;
    box-sizing: border-box;
    color: #fff;
    font-size: 12px;
    background-color: rgb(226, 64, 43);
    cursor: pointer;
}
.todo-list-check{
    margin-right: 10px;
}
</style>
