<!--  -->
<template>
    <div class="todo-footer-box">
        <p>
            <label>
                <!-- isSelectAll 监视 isSelectAll读取它的值，并且根据它的值去改变其他项-->
                <input type="checkbox" v-model="isSelectAll">
            </label>
        
            <span>已完成 {{completeNum}}</span> / 
            <span>全部 {{list.length}}</span>
        </p>
        <span class="del-all-btn" v-show="completeNum" @click="delCompleted">清除已完成任务</span>
    </div>
</template>

<script>
export default {
data () {
    return {
        // isSelectAll:false
    }
},

props: {
    list:Array,
    selectAll:Function,
    delCompleted:Function
},

components: {},

created () {},

computed: {
    completeNum(){
        //遍历数组如果选中则相加，默认选中数量为0
        return this.list.reduce((total,item) => total + (item.complete?1:0),0)  
    },
    isSelectAll:{
        get(){
            return this.completeNum === this.list.length && this.completeNum>0
        },
        set(value){  // value就是当前checkbox最新的布尔值
            this.selectAll(value)
        }
    },
},

mounted (){},

methods: {}
}

</script>
<style>
.todo-footer-box{
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.del-all-btn{
    padding: 5px 10px;
    box-sizing: border-box;
    color: #fff;
    font-size: 15px;
    background-color: rgb(226, 64, 43);
    border-radius: 5px;
    cursor: pointer;
}
</style>
