<template>
    <div class="todo-add-box">
        <input type="text" @keyup.enter="addValue" placeholder="请输入任务内容，按回车提交" class="todo-add-ipt" ref="addIpt" v-model="title">
    </div>
</template>

<script>
export default {
data () {
    return {
        title:'',
    }
},

props: {
    addItem:Function,
},

components: {},

created () {},

computed: {},

mounted (){},

methods: {
    addValue(){
        console.log('add')
        const title = this.title.trim()
        console.log('title',title)
        // 检查输入合法性
        if(!title){
            alert('必须输入内容')
            return
        }
        // 根据输入生成一个对象
        const todo = {
            title,
            complete:false
        }
        // 添加到list
        // this.addItem(todo)
        this.$emit('addItem',todo)
        // 清除输入
        this.title = ''
    },
}
}

</script>
<style>
.todo-add-box{
    height: 40px;
}
.todo-add-ipt{
    display: block;
    height: 40px;
    width: 100%;
    text-indent: 20px;
}
</style>
